  opacity: 0.6;
}
/***** Navigation menu *****/
#menu a:hover {
  cursor: pointer;
}
.mm-listview {
  font-size: 18px;
  line-height: 28px;
}
.mm-listview > li.mm-vertical > .mm-next,
.mm-vertical .mm-listview > li > .mm-next {
  height: 48px;
}
.mm-listview > li.mm-vertical > .mm-next:after,
.mm-vertical .mm-listview > li > .mm-next:after {
  top: calc(46%);
  bottom: auto;
}
.mm-listview li i:not(.menu-icon),
#account i {
  margin-right: 10px;
}
.mm-listview span:hover {
  cursor: pointer;
}
.models i {
  opacity: 0;
}
.menu-icon-container {
  font-size: 50px;
  text-align: center;
  padding: 20px 40px !important;
}
.menu-icon-container.profile-picture {
  font-size: 1px;
  line-height: initial;
}
.avatar {
  margin: 0;
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.mm-navbar > a {
  font-size: 20px;
}
.mm-listview > li.car-icon:after {
  content: '';
  border-bottom-width: 1px;
  border-bottom-style: solid;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0px;
}
.efficiency-settings > li:not(.mm-divider):after {
  content: '';
  border-bottom-width: 0;
  border-bottom-style: solid;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 20px;
}
