#account {
  position: absolute;
  display: block;
  width: 100%;
  bottom: 0;
  font-size: 17px;
  z-index: 1;
  background-color: #f3f3f3;
  padding: 2px 15px;
}
.logout {
  text-decoration: underline;
  position: relative;
  font-size: 90%;
