/*   old    */
.dropbtn {
  background-color: transparent;
  color: black;
  padding: 0.1em 1.5em 0.25em;
  font-weight: bold;
  font-size: 18px;
  border: none;
  border-radius: 5px 5px 0 0;
  cursor: pointer;
  margin: 2px 0 2px 0;
}
.dropdown {
  position: relative;
  display: inline-block;
}
.dropdown-content {
  text-align: left;
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 175px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  border-radius: 0 0 5px 5px;
  z-index: 5;
}
.dropdown-content a {
  color: black;
  padding: 12px 22px;
  text-decoration: none;
  display: block;
}
.dropdown-content a:hover {
  background-color: #e6e6e6;
  cursor: pointer;
}
.dropdown:hover .dropdown-content {
  display: block;
}
.dropdown:hover .dropbtn {
  color: white;
  background-color: #3e8e41;
}
.checkmark_con,
.checkmark_pro {
  padding-right: 1em;
  visibility: hidden;
}
/*****About site******/
