  padding: 2px;
}
.about h2 {
  padding: 2px 0;
  position: relative;
}
.expand-h,
.expand-v,
.expand-h-small,
.expand-v-small {
  position: absolute;
  background-color: gray;
}
.expand-h {
  left: calc(70%);
  width: 20px;
  height: 4px;
  top: calc(48%);
}
.expand-v {
  left: calc(78%);
  width: 4px;
  height: 20px;
  top: calc(40%);
}
.expand-h-small {
  left: calc(77%);
  width: 16px;
  height: 4px;
  top: calc(13px);
}
.expand-v-small {
  left: calc(83%);
  width: 4px;
  height: 16px;
  top: calc(7px);
}
.about-cont {
  height: calc(50%);
  overflow-y: auto;
}
.about-heading {
  display: block;
  margin: auto;
  text-align: center;
}
.menu-title {
  position: relative;
  background-color: #E0E0E0;
  border-radius: 6px;
  border-width: 1px;
  border-color: white;
  border-style: solid;
  width: 100%;
  margin: 0;
