}
/***** Route overlay *****/
#route {
  /*position: absolute;*/
  text-align: center;
  top: 2.5em;
  /*width: 80%;*/
  width: 100%;
  /*margin: 0 10%;*/
  margin: 0;
  z-index: 1;
  display: block;
  vertical-align: middle;
}
#route a {
  display: block-inline;
  color: blue;
  text-decoration: underline;
  margin: auto;
}
.route-cont {
  position: relative;
  /*display: inline-block;*/
  display: block;
  height: 40px;
  /*margin: 0.25em;*/
  vertical-align: middle;
  height: 100%;
}
#route-cont {
  overflow-y: scroll;
  overflow-x: hidden;
  height: calc(55%);
}
.route-inputs-container {
  padding: 0 10px 0 5px;
}
.route-inputs-container + div {
  padding-bottom: 4px;
}
.route-input-cont {
  width: 100%;
  position: relative;
  padding: 5px 0px;
}
.route-input-marker {
  display: inline-block;
  width: 30px;
  vertical-align: middle;
  opacity: 0.5;
}
.route-input-breadcrumbs {
  position: absolute;
  top: 34px;
  left: 0;
  width: 30px;
  opacity: 0.5;
}
.route-input-add {
  position: absolute;
  top: 54px;
  left: 0;
  width: 30px;
  opacity: 0.5;
}
.route-input-cont:last-child .route-input-breadcrumbs {
  display: none;
}
.routeinput {
  display: inline-block;
  font-size: 20px;
  padding: 4px;
  height: 36px;
  width: calc(70%);
  box-sizing: border-box;
  border-width: 0 0 1px 0;
  border-color: #bfbfbf;
  border-style: solid;
  border-radius: 0;
  transition: all 0.3s;
  z-index: 1;
  vertical-align: middle;
}
.routeinput:focus {
  padding: 4px 26px 4px 4px;
}
.route-input-fade {
  position: absolute;
  background: linear-gradient(to right, rgba(255, 255, 255, 0), #ffffff);
  width: 40px;
  height: calc(82%);
  top: 9px;
  right: 4px;
  transition: all 0.3s;
  z-index: 2;
}
.routeinput:focus ~ .route-input-fade {
  right: 26px;
}
.route-input-clear {
  position: absolute;
  top: calc(39%);
  right: 4px;
  font-size: 22px;
  transition: visibility 0.3s ease-out 0s, opacity 0.3s ease-out 0s;
  z-index: 3;
  visibility: hidden;
  opacity: 0;
}
.route-input-clear:hover {
  cursor: pointer;
}
.routeinput:focus ~ .route-input-clear {
  visibility: visible;
  opacity: 0.65;
}
.route-waypoint-input {
  display: inline-block;
  width: calc(30%);
  vertical-align: middle;
  box-sizing: border-box;
}
.route-waypoint-cont {
  display: none;
}
.route-waypoint-cont .route-input-clear {
  right: 44px;
}
.route-waypoint-cont .route-input-fade {
  right: 44px;
}
.route-waypoint-cont .routeinput:focus ~ .route-input-fade {
  right: 66px;
}
.waypoint-remove-container {
  width: 40px;
  display: inline-block;
}
.waypoint-remove {
  opacity: 0.65;
  font-size: 22px;
  vertical-align: middle;
}
.waypoint-remove:hover {
  cursor: pointer;
}
#route-form input[type="submit"] {
  display: none;
}
.input-controls {
  display: none;
  width: 100%;
  padding: 5px 0;
}
.route-add {
  font-size: 20px;
  padding: 0.25em 0.6em;
  margin: 0 0.5em 0 0.5em;
  display: inline-block;
  height: 40px;
  vertical-align: middle;
  display: none;
}
#clear-route a {
  padding: 0.25em 0.5em;
  background-color: #f9f9f9;
  opacity: 1;
  border-radius: 5px;
}
.clear-route:hover {
  cursor: pointer;
}
#locateme-start {
  /*position: absolute;
	top: calc(50% - 15px);
	left: calc(100% - 31px);*/
  background-color: white;
  opacity: 1;
  margin: 0;
  padding: 0;
  text-align: center;
}
.locate-cont {
  height: 100%;
  display: inline-block;
  vertical-align: middle;
  background-color: white;
}
/***** RSS gen popup *****/
.rssgen {
  position: fixed;
  top: 85%;
  left: 50%;
  background-color: white;
  opacity: 1;
  z-index: 1;
  border-radius: 5px;
  text-align: center;
  display: none;
  padding: 5px 5px 5px 5px;
  margin-left: -155px;
  width: 300px;
}
.rssgen p {
  padding: 0;
  font-size: 14px;
  display: block-inline;
}
#rss-clear {
  text-align: center;
  padding-top: 5px;
}
#rss-clear:hover {
  color: blue;
  cursor: pointer;
}
#rss-response {
  width: 90%;
  font-size: 14px;
  display: none;
  margin: auto;
  text-align: center;
}
#rssgen-text:hover {
  cursor: pointer;
}
/***** Waypoints table *****/
#route-overview {
  width: calc(70%);
  max-width: 400px;
  max-height: 600px;
  min-height: 240px;
  height: calc(20%);
  background-color: white;
  z-index: 3;
  position: absolute;
  top: 60px;
  /*right: 100%;*/
  border-radius: 15px;
  text-align: center;
  display: none;
  margin-left: 15px;
  margin-top: 15px;
  -moz-transition: all 0.25s cubic-bezier(0.5, 0.25, 0.42, 1.28);
  -webkit-transition: all 0.25s cubic-bezier(0.5, 0.25, 0.42, 1.28);
  transition: all 0.25s cubic-bezier(0.5, 0.25, 0.42, 1.28);
  box-shadow: -5px 3px 15px grey;
}
.popup-window {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  border-radius: 15px;
  position: absolute;
  max-width: 400px;
  max-height: 500px;
  box-shadow: -5px 3px 15px grey;
  z-index: 5;
  background-color: white;
  transition: width 0.3s, height 0.3s;
  -moz-transition: width 0.3s, height 0.3s;
  -webkit-transition: width 0.3s, height 0.3s;
  transition-timing-function: ease;
  -moz-transition-timing-function: ease;
  -webkit-transition-timing-function: ease;
  overflow: hidden;
  display: none;
}
.small {
  max-width: 300px;
  top: 6%;
  bottom: initial;
}
.popup-window-title {
  text-align: center;
  padding: 0.5em;
  display: block;
}
.popup-window-content {
  height: calc(42%);
  padding: 0 1em;
  box-sizing: border-box;
  overflow-y: scroll;
}
.small .popup-window-content {
  overflow-y: hidden;
}
.popup-window > i {
  font-size: 30px;
  position: absolute;
  top: 0;
  left: calc(99%);
  z-index: 1;
  color: #a1a1a1;
}
.popup-window > i:hover {
  cursor: pointer;
}
#waypoint-table {
  display: none;
}
#waypoints-tab {
  overflow: hidden;
