.charge-suggest-cont {
  width: 100%;
  padding: 0.25em 0;
  margin: 0;
}
.charge-suggest-cont:hover {
  cursor: default;
}
.charge-suggest-title {
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  display: block;
  vertical-align: middle;
}
.charge-suggest-title:hover {
  background-color: #76d676;
  cursor: pointer;
}
.charge-suggest-title:nth-child(1):hover,
.charge-suggest-title:nth-last-child(2):hover,
.charge-suggest-title:last-child:hover {
  cursor: default;
  background-color: inherit;
}
.charge-suggest-info {
  text-align: center;
  font-size: 12px;
  display: block;
  padding: 5px 0;
}
.charge-suggest-power {
  display: inline-block;
  margin: auto 0;
  vertical-align: middle;
  width: 33%;
}
.charge-suggest-transition {
  padding: 5px 0;
}
.charge-suggest-info img {
  vertical-align: middle;
  padding-right: 4px;
}
.charge-suggest-userdata {
  font-size: 13px;
  padding: 5px;
  border-radius: 6px;
  position: relative;
}
.charge-suggest-userdata span {
  vertical-align: middle;
}
.charge-suggest-userdata img {
  vertical-align: middle;
  padding-right: 5px;
}
.comments-title {
  z-index: 1;
}
.comments-title:hover {
  cursor: pointer;
}
.comment-icon {
  opacity: 0.75;
  float: left;
}
.no-comments {
  display: none;
}
.charge-suggest-comment-container {
  display: none;
  padding-top: 5px;
  clear: both;
}
.charge-suggest-comment-container > span {
  font-size: 11px;
  font-style: italic;
  font-weight: bold;
}
.charge-suggest-comment {
  font-size: 12px;
  border-radius: 20px;
  margin: 8px 0;
  padding: 5px 2px;
  min-height: 35px;
  background-color: whitesmoke;
