  content: '';
  position: absolute;
  border-style: solid;
  border-width: 10px 0 10px 18px;
  border-top-color: transparent;
  border-bottom-color: transparent;
  border-left-color: whitesmoke;
  border-right-color: whitesmoke;
  display: block;
  width: 0;
  z-index: 1;
  right: -16px;
  top: 10px;
}
