.onoffswitch {
  position: relative;
  width: 46px;
  margin: auto;
  height: 25px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.onoffswitch-checkbox {
  display: none;
}
.onoffswitch-label {
  display: block;
  overflow: hidden;
  cursor: pointer;
  height: 100%;
  padding: 0;
  /*line-height: 21px;*/
  border: 2px solid #CCCCCC;
  border-radius: 21px;
